.ied_center_forget_conf_pass_password_hide_and_view {
  /* background-color: rgb(116, 38, 38); */
  width: 25px;
  height: 22px;
  position: absolute;
  top: 4px;
  right: 10px;
  cursor: pointer;
  z-index: 2;
  display: flex;
  justify-content: center;
  align-items: center;
}
.ied_center_forget_conf_pass_password_hide_and_view::before {
  content: "";
  background-color: #ffffff;
  width: 12px;
  height: 20px;
  position: absolute;
  filter: blur(4px);
  left: -10px;
}
.ied_center_forget_conf_pass_password_hide_and_view::after {
  content: "";
  background-color: #ffffff;
  width: 14px;
  height: 20px;
  position: absolute;
  right: -10px;
}

#ied_center_forget_conf_pass_password_hide_and_view_svg_see {
  color: #a50808;
}

#ied_center_forget_conf_pass_password_hide_and_view_svg_notSee {
  /* color: #127e63; */
  color: #3a3a3a;
}
